<template>
  <div id="myDay">
    <nav-bar>
      <input
        slot="center"
        class="search"
        type="text"
        placeholder="Ê∑ªÂä†todo"
        v-model="inputValue"
        @keyup.enter="addItem"
      />
    </nav-bar>
    <task-bar class="todo">
      <span
        slot="tagging"
        @mouseover="showDetailM('todo')"
        @mouseout="hideDetailM('todo')"
        @click="clearItem(false)"
      >
        ToDo({{ todoCount }})
        <show-detail
          text="ÁÇπÊàëÊ∏ÖÁ©∫"
          :display="displayTodo"
          top="0.2rem"
          left="1rem"
        ></show-detail>
      </span>
      <span v-if="!todoCount">‰Ω†ËøòÊ∑ªÂä†‰ªª‰ΩïÂæÖÂäû</span>
      <transition-group name="ani">
        <list-item
          v-for="(item, index) in mydayList"
          :key="item.tid"
          v-show="!item.done"
          :item="item.title"
          :done="item.done"
          @deleteItem="deleteItem(index)"
          @isDone="isDone(index)"
        ></list-item>
      </transition-group>
    </task-bar>
    <task-bar class="done">
      <!-- <task-bar :tagging="'Done' + '(' + doneCount + ')'" class="done" > -->
      <span
        slot="tagging"
        @mouseover="showDetailM('done')"
        @mouseout="hideDetailM('done')"
        @click="clearItem(true)"
      >
        Done({{ doneCount }})
        <show-detail
          text="ÁÇπÊàëÊ∏ÖÁ©∫"
          :display="displayDone"
          top="0.2rem"
          left="1rem"
        ></show-detail>
      </span>
      <span v-if="!doneCount">‰Ω†ËøòÊú™ÂÆåÊàê‰ªª‰ΩïÂæÖÂäû</span>
      <transition-group name="ani">
        <list-item
          v-for="(item, index) in mydayList"
          :key="item.tid"
          v-show="item.done"
          :item="item.title"
          :done="item.done"
          @deleteItem="deleteItem(index)"
          @isDone="isDone(index)"
        ></list-item>
      </transition-group>
    </task-bar>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar.vue";
import TaskBar from "components/content/taskbar/TaskBar.vue";
import ListItem from "components/content/listItem/ListItem.vue";
import ShowDetail from "components/content/showDetail/ShowDetail.vue";

import {
  getData,
  saveData,
  deleteData,
  getDateStr,
} from "assets/js/todoList.js";

export default {
  name: "MyDay",
  components: {
    NavBar,
    TaskBar,
    ListItem,
    ShowDetail,
  },
  data() {
    return {
      mydayList: [
        // {title: 'üçéÂêÉËãπÊûú', done: false, createDate: "2021-03-26"},
        // {title: 'üçìÂêÉËçâËéì', done: false, createDate: "2021-03-26"},
        // {title: 'üçåÂêÉÈ¶ôËïâ', done: false, createDate: "2021-03-26"},
        // {title: 'üçàÂêÉÊ©òÂ≠ê', done: false, createDate: "2021-03-26"},
        // {title: 'üçâÂêÉË•øÁìú', done: false, createDate: "2021-03-26"},
      ],
      inputValue: "",
      dateNow: "",
      displayTodo: "none",
      displayDone: "none",
    };
  },
  computed: {
    //Êú™ÂÆåÊàêÊï∞Èáè
    todoCount() {
      return this.mydayList.filter((item) => {
        return !item.done;
      }).length;
    },

    //Â∑≤ÂÆåÊàêÊï∞Èáè
    doneCount() {
      return this.mydayList.filter((item) => {
        return item.done;
      }).length;
    },
  },
  // È°µÈù¢Âä†ËΩΩÂç≥Ê∏≤ÊüìÊâÄÊúâÊï∞ÊçÆ
  created() {
    let allList = getData("myday");
    this.mydayList = allList;
  },

  //ÁõëÂê¨Êï∞ÊçÆÂèòÂåñ
  watch: {
    mydayList: {
      // ËØ•ÂõûË∞É‰ºöÂú®‰ªª‰ΩïË¢´‰æ¶Âê¨ÁöÑÂØπË±°ÁöÑ mydayList ÊîπÂèòÊó∂Ë¢´Ë∞ÉÁî®Ôºå‰∏çËÆ∫ÂÖ∂Ë¢´ÂµåÂ•óÂ§öÊ∑±
      handler() {
        saveData(this.mydayList, "myday");
      },
      deep: true,
    },
  },

  methods: {
    // Ê∑ªÂä†
    addItem(e) {
      let data = e.target.value;
      if (data === "") {
        return alert("ËØ∑ËæìÂÖ•ÂæÖÂäû");
      }
      const createDate = getDateStr();
      const tid = new Date().valueOf();
      this.mydayList.push({ title: data, done: false, createDate, tid });
      this.inputValue = "";
    },
    //Âà†Èô§
    deleteItem(index) {
      this.mydayList.splice(index, 1);
    },
    // Ê∏ÖÈô§ÂæÖÂäû
    clearItem(done) {
      let sure = confirm("Á°ÆÂÆöÊ∏ÖÁ©∫ËØ•Á±ªÂæÖÂäûÔºü");
      if (sure) {
        this.mydayList = this.mydayList.filter((item) => {
          return item.done !== done;
        });
      }
    },
    //ÂÆåÊàê
    isDone(index) {
      this.mydayList[index].done = !this.mydayList[index].done;
    },
    // Â±ïÁ§∫ÊèêÁ§∫‰ø°ÊÅØ
    showDetailM(type) {
      switch (type) {
        case "todo":
          this.displayTodo = "block";
          break;
        case "done":
          this.displayDone = "block";
          break;
        default:
          this.displayTodo = "block";
          break;
      }
    },
    // ÈöêËóèÊèêÁ§∫‰ø°ÊÅØ
    hideDetailM(type) {
      switch (type) {
        case "todo":
          this.displayTodo = "none";
          break;
        case "done":
          this.displayDone = "none";
          break;
        default:
          this.displayTodo = "none";
          break;
      }
    },
  },
  // ÁªÑ‰ª∂Â§Ñ‰∫éÊ¥ªË∑ÉÁä∂ÊÄÅÊó∂Ë∞ÉÁî®ÁöÑÂáΩÊï∞
  activated() {
    this.mydayList = getData("myday");
  },
};
</script>

<style scoped lang="less">
#myDay {
  .todo {
    width: 3.6rem;
    position: absolute;
    right: 5.28rem;
  }
  .done {
    width: 3.6rem;
    position: absolute;
    right: 0.75rem;
  }
}
</style>